.BillingForm .card-field {
  margin-bottom: 15px;
  background-color: white;
  padding: 11px 16px;
  border-radius: 6px;
  border: 1px solid #ccc;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  line-height: 1.3333333;
}

.BillingForm .card-field.StripeElement--focus {
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 8px rgba(102, 175, 233, 0.6);
  border-color: #66afe9;
}

.Evolutions {
  margin-top: 10px;
}

.card-link {
}
a.card-link {
  color: #5c0115;
}
a.card-link:hover,
a.card-link:active {
  color: #870e1c;
}

.textCanvas {
  /*width: 800px;
  height: 450px;*/
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.canvas {
  position: relative;
  margin: 10px auto 0 auto;
  text-align: center;
  width: 800px;
  height: 450px;
  background-color: black;
}
.video {
  width: 800px;
  height: 450px;
  display: none;
}
.videoCanvas {
  /*width: 800px;
  height: 450px;*/
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}
.playButton {
  position: absolute;
  top: 175px;
  left: 375px;
  z-index: 3;
  max-width: 100px;
  height: auto;
}
.progressBar {
  position: absolute;
  bottom: 0;
  z-index: 4;
  width: 100%;
  border-radius: 0;
  background-color: black;
}

.NotFound {
  padding-top: 100px;
  text-align: center;
}

